
#BBSWindow @font-face {
  font-family: SymMingLiu;
  src: url(/jbv_dev/static/fonts/symmingliu.aaa394d.woff) format("woff")
}
#BBSWindow #picPreview {
  position: absolute;
  display: none;
  z-index: 3
}
#BBSWindow #sideMenus {
  position: fixed;
  width: 95px;
  top: 0;
  z-index: 3;
  -webkit-transition: .25s ease;
  transition: .25s ease
}
#BBSWindow .menu {
  left: 0
}
#BBSWindow .menuHidden {
  left: -85px
}
#BBSWindow .menuHidden:hover {
  left: 0
}
#BBSWindow #logo {
  width: 85px;
  height: 90px;
  background: url(data:image/png;base64,) 0 0/85px 85px no-repeat
}
#BBSWindow .font_btn {
  width: 85px;
  height: 85px;
  background-color: #1a1a1a;
  text-align: center;
  padding: 10px;
  margin: 0;
  float: left;
  cursor: pointer
}
#BBSWindow .font_btn:hover {
  background-color: #333
}
#BBSWindow .font_btn>i {
  line-height: 40px
}
#BBSWindow .font_btn>span {
  font-size: 13px
}
#BBSWindow {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 1;
  -webkit-user-select: none
}
#cursor {
  display: none
}
.blink--active #cursor {
  display: block
}
#BBSWindow #easyReadingLastRow,
#BBSWindow #easyReadingReplyRow,
.main {
  font-family: MingLiu, monospace;
  font-size: 26px;
  line-height: 100%;
  margin: 0;
  -webkit-user-select: text;
  background-color: #000
}
#BBSWindow #easyReadingLastRow *,
#BBSWindow #easyReadingReplyRow *,
#BBSWindow .main * {
  font-family: inherit !important
}
#BBSWindow .linkurl {
  text-decoration: underline;
  color: #f0f
}
#BBSWindow a:active,
#BBSWindow a:hover,
#BBSWindow a:link,
#BBSWindow a:visited {
  background-position: 0 90%;
  text-decoration: none
}
#BBSWindow #easyReadingLastRow>span,
#BBSWindow #easyReadingReplyRow>span,
#BBSWindow #mainContainer>span,
#BBSWindow .main>span {
  display: block;
  color: silver;
  white-space: pre
}
#BBSWindow #easyReadingLastRow,
#BBSWindow #easyReadingReplyRow {
  position: relative;
  display: none;
  overflow: hidden;
  -webkit-user-select: none;
  z-index: 1;
  height: 1em;
  background-color: transparent
}
#BBSWindow #easyReadingLastRow {
  margin-top: -1em
}
#BBSWindow #easyReadingReplyRow {
  margin-top: -2em
}
#BBSWindow #fbSharing {
  position: absolute;
  display: none;
  z-index: 2
}
#BBSWindow #fbSharing .fb_iframe_widget span iframe {
  bottom: 0 !important
}
#BBSWindow .easyReadingImg {
  max-height: 19em;
  max-width: 39em;
  margin: .5em auto;
  display: block
}
#BBSWindow .easyReadingVideo {
  margin: .5em auto;
  width: 640px;
  display: block
}
#BBSWindow .terminal_display {
  font-family: MingLiu, monospace;
  font-size: 26px;
  line-height: 26px;
  white-space: pre;
  cursor: text;
  position: absolute;
  color: #fff;
  text-decoration: blink;
  -webkit-user-select: none;
  height: 0;
  z-index: 2
}
#BBSWindow img {
  background: #fff;
  color: #000;
  padding: 5px
}
#BBSWindow .main img {
  padding: 0
}
#BBSWindow {
  color: #fff; //background-color:#101010;background-color:#000;margin:0;overflow:hidden
}
span [link=true]{margin:0;background-image:inherit;background-repeat:inherit;background-position:inherit
}
span{margin:0
}
.drag{display:none;position:absolute;cursor:default;z-Index:11
}
.closeSI{float:right;background-color:#bbf
}
.closeSI,.EPbtn{margin:0;border:0;padding:1px;cursor:default
}
.EPbtn{background-color:#fbb
}
.closePP{margin:0;border:0;padding:1px;background-color:#bfb;cursor:default
}
#PicturePreviewInfo{color:#fff;text-shadow:0 0 1px #fff,0 0 2px #fff,0 0 3px #fff,0 0 4px #00f,0 0 7px #00f,0 0 8px #00f,0 0 10px #00f,0 0 15px #00f
}
#SymbolInput{background-color:#bbf;opacity:.75
}
#SymbolInput,#YoutubeWindow,#YoutubeWindow2{left:10px;top:10px;z-index:3;padding:3px;border:0 double #ccc;-moz-border-radius:8px;cursor:default
}
#YoutubeWindow,#YoutubeWindow2{background-color:#fbb
}
#PictureWindow{left:10px;top:10px;z-index:3;background-color:#fff;padding:3px;border:0 double #ccc;-moz-border-radius:8px;cursor:default
}
.floatWindowClientArea{background-color:#fff;margin:22px 3px 3px;border:1px solid #000;cursor:default
}
.floatWindowClientArea2{margin:22px 3px 0
}
.floatWindowClientArea2,.floatWindowClientArea3{background-color:#fbb;border:0 solid #000;cursor:default
}
.floatWindowClientArea3{margin:0 3px 3px
}
.floatWindowClientArea4{background-color:#fff;position:relative
}
.floatWindowClientArea4,.floatWindowClientArea5{margin:0;padding:0;border:0 solid #000;cursor:default
}
.floatWindowClientArea5{background-color:#bfb
}
.picturePreview{background:#fff;color:#000;padding:0;margin:0
}
.sBtn{font-family:MingLiu,monospace;font-size:12px;cursor:default;min-width:26px;margin:2px
}
.main::-webkit-scrollbar{width:10px
}
.main::-webkit-scrollbar-track{background:hsla(0,0%,100%,.1)
}
.main::-webkit-scrollbar-track:hover{background:hsla(0,0%,100%,.2)
}
.main::-webkit-scrollbar-thumb{background:hsla(0,0%,100%,.3)
}
.main::-webkit-scrollbar-thumb:hover{background:hsla(0,0%,100%,.5)
}
.glyphicon-refresh-animate{-webkit-animation:spin .7s linear infinite
}
@-webkit-keyframes spin{
0%{-webkit-transform:rotate(0deg)
}
to{-webkit-transform:rotate(1turn)
}
}
.modal-backdrop{z-index:1!important
}
